    <div class="flybox">
      <el-input 
        placeholder="请输入经纬度(纬度，经度)" 
        v-model="latLngVal" 
        @keyup.enter.native="latLngChange">
      </el-input>
      <el-button @click="latLngChange">GO</el-button>
