<script>
import "leaflet";
import "leaflet.chinatmsproviders"
import access from "@/core/access.js"
import baseLayers from "@/core/mapData"

export default {
  name: "Home",
  data() {
    return {
      mymap: undefined, // 地图保存变量
      latLngVal: "",
      options: [
        {
          value: "mapbox",
          label: "mapbox"
        },
        {
          value: "TianDiTu",
          label: "天地图"
        }
      ],
      value: "mapbox"
    };
  },
  mounted() {
    // 初始化地图
    this.mymap = L.map("map", {
        center: [31.59, 120.29],
        zoom: 12,
        layers: [normalm1],
        zoomControl: false
    });

    // 利用教程自带的瓦片地图进行展示
    L.tileLayer(access.mapbox.url, access.mapbox.config).addTo(this.mymap);

    L.control.layers(baseLayers, null).addTo(this.mymap);
  },
  methods: {
    latLngChange() {
      // 存放经纬度数组的临时变量
      let latLngTmp = this.latLngVal
        .replace("，", ",")
        .split(",")
        .map(v => parseFloat(v));
      try {
        this.mymap.flyTo(latLngTmp);
        this.$message({
          showClose: true,
          message: "坐标切换到" + latLngTmp
        });
      } catch (error) {
        this.$message({
          showClose: true,
          message: "坐标切换失败"
        });
      }
    },
